import { Edit } from "@mui/icons-material";
import { Box, Button, Grid, Modal, TextField, Typography } from "@mui/material";
import { useRef, useState } from "react";
import "react-phone-input-2/lib/material.css";
import { useLocation, useNavigate } from "react-router-dom";
import color from "../../components/color";
import { verifyOTP } from "../../services/services";
import "./Login.css";

import { toast } from "react-toastify";
import {
  setCurrentAccessToken,
  setCurrentUser,
} from "../../services/axiosClient";

interface LoginOtpModalProps {
  open: boolean;
  onClose: () => void;
  onVerificationSuccess: () => void;
  phone: string;
  name: string;
  email: string;
  token: string;
}

const LoginOtpModal = ({ open,
  onClose,
  onVerificationSuccess,
  phone,
  name,
  email,
  token
}: LoginOtpModalProps) => {
  const [step, setStep] = useState(1);
  const [phoneNumber, setPhoneNumber] = useState("");
  const [countryCode, setCountryCode] = useState("in");
  const [sessionId, setSessionId] = useState("");
  const navigate = useNavigate();

  const location = useLocation();
  // const state = location.state;
  // const queryParams = new URLSearchParams(location.search);
  // const phone = queryParams.get("phone");
  // const name = queryParams.get("name");
  // const session = queryParams.get("token");
  // const email = queryParams.get("email");

  const [otp, setOtp] = useState(Array(4).fill(""));
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const otpRefs = Array.from({ length: 4 }, () =>
    // eslint-disable-next-line react-hooks/rules-of-hooks
    useRef<HTMLInputElement>(null)
  );

  const isModalOpen =
    new URLSearchParams(location.search).get("login") === "true";

  const handleClose = () => {
    const searchParams = new URLSearchParams(location.search);

    searchParams.delete("login");
    searchParams.delete("phone");
    searchParams.delete("name");
    searchParams.delete("email");
    searchParams.delete("token");

    navigate(`${location.pathname}?${searchParams.toString()}`, {
      replace: true,
      state: { ...location.state },
    });
  };

  // const phoneFormik = useFormik({
  //   initialValues: { phone: "" },
  //   validationSchema: Yup.object({
  //     phone: Yup.string()
  //       .required("Phone number is required")
  //       .matches(/^\d+$/, "Only numbers allowed"),
  //   }),
  //   onSubmit: (values) => {

  //   },
  // });

  const handleOtpChange = (index: number, value: string) => {
    if (!/^\d*$/.test(value)) return;
    const newOtp = [...otp];
    newOtp[index] = value;
    setOtp(newOtp);

    if (value && index < 3) {
      otpRefs[index + 1].current?.focus();
    }
  };

  const handleOtpSubmit = () => {
    if (otp.join("").length === 4) {
      verifyOTP({
        otp: otp.join(""),
        sessionId: token,
        phone,
        name,
        email
      })
        .then((res) => {
          setCurrentAccessToken(res?.data?.data?.token);
          setCurrentUser(res?.data?.data?.user);
          toast.success("Verified successfully!");
          onVerificationSuccess(); // Call the success callback
          onClose();
        })
        .catch((err) => {
          toast.error("Verification failed");
        });
    }
  };

  return (
    <Modal open={open} onClose={onClose}>
      <Box sx={modalStyle}>
        {/* {step === 1 ? (
          <form
            onSubmit={phoneFormik.handleSubmit}
            style={{ height: "100%", borderRadius: "12px" }}
          >
            <div
              className="subscribe"
              style={{
                background: color.background,
                color: "white",
                position: "relative",
                boxShadow: "-4px -4px 10px rgba(255, 255, 255, 0.36) inset",
                borderRadius: "12px",
                overflow: "hidden",
              }}
            >
              <Box
                sx={{
                  background: "url('/assets/footer.webp')",
                  backgroundSize: { xs: "70%", md: "70%" },
                  backgroundRepeat: "no-repeat",
                  backgroundPosition: "bottom left",
                  position: "absolute",
                  top: 0,
                  left: 0,
                  width: "100%",
                  height: "100%",
                }}
              ></Box>

              <Typography variant="h6">Enter Phone Number</Typography>
              <Typography
                mb={4}
                variant="body2"
                sx={{ fontSize: "12px", textAlign: "left", maxWidth: "80%" }}
              >
                Login to create a booking!
              </Typography>
              <PhoneInput
                country={"in"}
                value={phoneFormik.values.phone}
                onChange={(value, country) => {
                  const countryData = country as CountryData;
                  setCountryCode(countryData.countryCode);
                  phoneFormik.setFieldValue(
                    "phone",
                    value.replace(/[^0-9]/g, "")
                  );
                }}
                inputStyle={{
                  width: "100%",
                  fontFamily: "CustomFontM",
                  color: "black",
                  background: color.thirdColor,
                }}
                containerStyle={{ position: "relative" }}
              />
              {phoneFormik.touched.phone && phoneFormik.errors.phone && (
                <Typography sx={{ color: "white", fontSize: "14px" }}>
                  *{phoneFormik.errors.phone}
                </Typography>
              )}

              <Button
                type="submit"
                sx={{
                  background: color.thirdColor,

                  color: color.firstColor,
                  fontSize: "14px",
                  fontWeight: "bold",
                  textTransform: "none",
                  m: "auto",
                  mt: 4,
                  width: "100%",
                  display: "block",
                  position: "absolute",
                  bottom: 0,
                  left: 0,
                  borderRadius: 0,
                  py: 2,
                  boxShadow: "0px -6px 50px rgba(7, 7, 7, 0.11)",
                }}
              >
                {" "}
                Send OTP
              </Button>

              {/* <div className="submit-btn">SUBMIT</div> */}
        {/* </div> */}
        {/* </form> */}
        {/* ) : (  */}
        <>
          <div
            className="subscribe"
            style={{
              background: color.background,
              color: "white",
              position: "relative",
              boxShadow: "-4px -4px 10px rgba(255, 255, 255, 0.36) inset",
              borderRadius: "12px",
              overflow: "hidden",
            }}
          >
            <Box
              sx={{
                background: "url('/assets/footer.webp')",
                backgroundSize: { xs: "70%", md: "70%" },
                backgroundRepeat: "no-repeat",
                backgroundPosition: "bottom left",
                position: "absolute",
                top: 0,
                left: 0,
                width: "100%",
                height: "100%",
              }}
            ></Box>
            <Typography variant="h6">Enter OTP</Typography>
            <Typography variant="body2" sx={{ mb: 1 }}>
              Sent to: +{phone}{" "}
              <Button
                size="small"
                onClick={() => handleClose()}
                sx={{
                  color: color.firstColor,
                  background: color.thirdColor,
                  p: 0,
                  px: 1,
                  minWidth: 0,
                  ml: 1,
                  textTransform: "none",
                }}
              >
                <Edit sx={{ fontSize: "14px" }}></Edit> Edit
              </Button>
            </Typography>
            <Grid container spacing={1} justifyContent="center" mt={3}>
              {otp.map((_, index) => (
                <Grid item key={index}>
                  <TextField
                    inputRef={otpRefs[index]}
                    value={otp[index]}
                    onChange={(e) => handleOtpChange(index, e.target.value)}
                    inputProps={{
                      maxLength: 1,
                      style: {
                        textAlign: "center",
                        fontSize: 18,
                        borderColor: "transparent",
                      },
                    }}
                    // sx={{ width: 40 }}

                    sx={{
                      width: 40,
                      bgcolor: color.thirdColor,
                      borderRadius: 2,
                      // minWidth: "200px",
                      border: "none",
                      outline: "none",
                      boxShadow: "none",
                      "& fieldset": {
                        border: "none",
                      },
                      "&:hover": {
                        bgcolor: "#f5f5f5",
                      },
                      "& .MuiInputBase-input": {
                        color: color.firstColor,
                        fontFamily: "CustomFontB",
                        fontSize: "20px",
                      },
                    }}
                  />
                </Grid>
              ))}
            </Grid>
            <Button
              onClick={handleOtpSubmit}
              type="submit"
              sx={{
                background: color.thirdColor,
                color: color.firstColor,
                fontSize: "14px",
                fontWeight: "bold",
                textTransform: "none",
                m: "auto",
                mt: 4,
                width: "100%",
                display: "block",
                position: "absolute",
                bottom: 0,
                left: 0,
                borderRadius: 0,
                py: 2,
                boxShadow: "0px -6px 50px rgba(7, 7, 7, 0.11)",
              }}
            >
              Verify OTP
            </Button>
          </div>
        </>
        {/* )} */}
      </Box>
    </Modal>
  );
};

const modalStyle = {
  position: "absolute" as "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  maxWidth: 400,
  width: "90%",
  height: 240,
  background: color.thirdColor,
  borderRadius: "12px",
  //   p: 4,
};

export default LoginOtpModal;
